import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {OraModal} from "eshop/components/OraCommonComponents";
import {ResponsiveVisibility} from "eshop/modules/core/components/ui/RWDUtils";
import {RwdPriceBox} from "eshop/modules/cart/components/entriesList/shared/PriceBoxes";
import {transformPriceInfo, transformOneTimePriceInfo} from "eshop/modules/cart/components/entriesList/fix/buymore/Utils";

class FixDetailsModal extends Component  {
    constructor (props) {
        super(props);
    }

    renderSimple(){
        const detailsHTML = {__html: this.props.details};
        return (this.props.details &&
            <div dangerouslySetInnerHTML={detailsHTML}/>
        );
    }

    render(){
        const details = this.renderSimple();
        return(
        <OraModal id={this.props.id} showClose={true} narrow={true}>
            <div>
                <VASDetailsHeader {...this.props}/>
                <div className="l-full-row">
                    <div className="l-col l-col-10 l-col-medium-12 l-col-small-12 large-offset-by-2 u-small-margin-top u-margin">
                        <div className="o-separator"></div>
                    </div>
                </div>
                <div className="l-full-row">
                    <div className="l-col l-col-10 large-offset-by-2">
                        {details}
                    </div>
                </div>
            </div>
        </OraModal>)
    }
}

FixDetailsModal.defaultProps = {
    id: "",
    details: "",
    header : "default: Szczegóły usługi"
};

FixDetailsModal.propTypes = {
     id: PropTypes.string,
     icon: PropTypes.string,
     details: PropTypes.string,
     productName: PropTypes.string,
     header: PropTypes.string,
     prices: PropTypes.arrayOf(PropTypes.object)
};

const VASDetailsHeader = (props) => {
    const icon = props.icon ? props.icon : "funpack-2";
    const iconSize = props.icon && props.icon.indexOf(" ") > -1 ? "" : " g-icon--l";
    return(
        <div>
            <p className="h3 u-spacing-l">{props.header}</p>
            <VASDetailsDesktopHeader icon={icon} iconSize={iconSize} productName={props.productName} vas={props.vas} net={props.net}/>
            <VASDetailsMobileHeader icon={icon} iconSize={iconSize} productName={props.productName} vas={props.vas} net={props.net}/>
        </div>
    );
};

const VASDetailsDesktopHeader = (props) => {

    return (<ResponsiveVisibility mobile={false}>
        <div className="l-full-row">
            <div className="l-col l-col-2">
                <span className={"u-padding-top-l u-padding-m u-large-left u-medium-left g-icon g-icon--only g-icon--" + props.icon + props.iconSize}/>
            </div>
            <div className="l-col l-col-6">
                <p className="h5 u-spacing-top-l">{props.productName}</p>
                {props.vas && props.vas.slogan && <p className="u-margin" dangerouslySetInnerHTML={{__html: props.vas.slogan}}></p>}
            </div>
            <div className="l-col l-col-4">
                {props.vas && <RwdPriceBox
                    prices={transformPriceInfo(props.vas.monthlyPrices, props.net)}
                    oneTimePrice={transformOneTimePriceInfo(props.vas.firstBillPrice, props.net)}
                    checkoutPrice={transformOneTimePriceInfo(props.vas.checkoutPrice, props.net)}
                /> }
            </div>
        </div>
    </ResponsiveVisibility>);
};


const VASDetailsMobileHeader = (props) => {
    return (<ResponsiveVisibility desktop={false} mobile={true}>
        <div className="l-full-row">
            <div className="l-col l-col-5">
                <span className={"g-icon g-icon--only g-icon--" + props.icon + props.iconSize}/>
            </div>
            <div className="l-col l-col-7">
                {props.vas && <RwdPriceBox
                    prices={transformPriceInfo(props.vas.monthlyPrices, props.net)}
                    oneTimePrice={transformOneTimePriceInfo(props.vas.firstBillPrice, props.net)}
                    checkoutPrice={transformOneTimePriceInfo(props.vas.checkoutPrice, props.net)}
                /> }
            </div>
            <div className="l-col l-col-12">
                <p className="h5 u-spacing-top-m">{props.productName}</p>
                {props.vas && props.vas.slogan && <p className="u-margin" dangerouslySetInnerHTML={{__html: props.vas.slogan}}></p>}
            </div>
        </div>
    </ResponsiveVisibility>);
};

export default FixDetailsModal;